
































































































































































































import {Component, Vue, Watch} from 'vue-property-decorator';
import SideMenu from '@/components/SideMenu.vue';
import SplashScreen from '@/components/SplashScreen.vue';


declare let $: any;

/**
 * Resize function without multiple trigger
 *
 * Usage:
 * $(window).smartresize(function(){
 *     // code here
 * });
 */
(function ($, sr) {
    // debouncing function from John Hann
    // http://unscriptable.com/index.php/2009/03/20/debouncing-javascript-methods/
    var debounce = function (func, threshold?, execAsap?) {
        var timeout;

        return function debounced() {
            var obj = this, args = arguments;

            function delayed() {
                if (!execAsap)
                    func.apply(obj, args);
                timeout = null;
            }

            if (timeout)
                clearTimeout(timeout);
            else if (execAsap)
                func.apply(obj, args);

            timeout = setTimeout(delayed, threshold || 100);
        };
    };

    // smartresize
    $.fn[sr] = function (fn) {
        return fn ? this.bind('resize', debounce(fn)) : this.trigger(sr);
    };

})($, 'smartresize');

/**
 * Gentelella scripts
 */

@Component({
    components: {
        'SideMenu': SideMenu,
        'SplashScreen': SplashScreen
    }
})
export default class App extends Vue {

    @Watch('$route.path')
    onChangeRouterPath(path, oldPath) {
        let me = this;
        me.documentUp();
        if (path === '/') {
            me.$store.dispatch('setBreadcrumbs', {
                items: [{
                    name: 'Главная',
                    url: false
                }],
                page_title: 'FastDog CMS'
            })
        }
    }

    documentUp(): void {
        $('html, body').animate({
            scrollTop: 0
        }, 800);
    }

    static baseUrl() {
        return window.location.protocol + '//' + window.location.hostname;
    }

    getLogo(): string {
        return window.location.protocol + '//' + window.location.hostname + '/vendor/fast_dog/frontend/img/logo.png';
    }

    isHome(): boolean {
        return this.$route.name == 'home';
    }

    mounted(): void {
        let me = this;

        me.$store.dispatch('setBreadcrumbs', {
            items: [{
                name: 'Главная',
                url: false
            }],
            page_title: 'FastDog CMS'
        });

        document.body.classList.add('nav-md', 'footer_fixed');

        $('.collapse-link').on('click', function () {
            var $BOX_PANEL = $(this).closest('.x_panel'),
                $ICON = $(this).find('i'),
                $BOX_CONTENT = $BOX_PANEL.find('.x_content');

            // fix for some div with hardcoded fix class
            if ($BOX_PANEL.attr('style')) {
                $BOX_CONTENT.slideToggle(200, function () {
                    $BOX_PANEL.removeAttr('style');
                });
            } else {
                $BOX_CONTENT.slideToggle(200);
                $BOX_PANEL.css('height', 'auto');
            }

            $ICON.toggleClass('fa-chevron-up fa-chevron-down');
        });

        $('.close-link').click(function () {
            var $BOX_PANEL = $(this).closest('.x_panel');

            $BOX_PANEL.remove();
        });

        // $('[data-toggle="tooltip"]').tooltip({
        //     container: 'body'
        // });

    }
}
